import { Select as SelectComponent } from '@material-ui/core'
import React from 'react'
import { Props } from './model'
import useStyles from './useStyles'

const Select: React.FC<Props> = ({ children, width, value, defaultValue, ...restProps }) => {
  const classes = useStyles({ isSelected: Boolean(value) || Boolean(defaultValue) })

  return (
    <SelectComponent
      {...restProps}
      value={value}
      defaultValue={defaultValue}
      style={{ width }}
      classes={{ root: classes.select, icon: classes.icon }}
    >
      {children}
    </SelectComponent>
  )
}

export default Select
