import React from 'react'
import { Box, Drawer, Typography } from '@material-ui/core'
import closeSrc from 'src/assets/icon/close-bold.svg'
import useStyles from './useStyles'

interface Props {
  open: boolean
  title: string
  onClose?: () => void
  [key: string]: unknown
}

const Container: React.FC<Props> = ({ open, onClose, title, children }) => {
  const classes = useStyles()

  return (
    <Drawer classes={{ paper: classes.container }} anchor='right' open={open} onClose={onClose}>
      <Box height={64} className={classes.titleContainer}>
        <Typography variant='h5' className={classes.title}>
          {title}
        </Typography>
        <button className={classes.closeIcon} onClick={onClose}>
          <img src={closeSrc} alt='' />
        </button>
      </Box>
      {children}
    </Drawer>
  )
}

const Content: React.FC = ({ children }) => {
  const classes = useStyles()

  return (
    <Box className={classes.content} overflow='auto'>
      {children}
    </Box>
  )
}

const Footer: React.FC = ({ children }) => {
  const classes = useStyles()

  return (
    <Box height={64} className={classes.control}>
      {children}
    </Box>
  )
}

export default { Container, Footer, Content }
