import React from 'react'
import upArrowSrc from 'src/assets/icon/up.svg'
import downArrowSrc from 'src/assets/icon/down.svg'
import { Props } from './model'
import { AccountOrder } from 'src/pages/Account/model'
import { OrderChange } from 'src/pages/Role/machineConfig/event'
import useStyles from './useStyles'

const SortButtons: React.FC<Props> = ({ sortUp, sortDown, sortMethod }) => {
  const classes = useStyles()
  const typedSortMethod = sortMethod as (orderBy: OrderChange['order_by'] | AccountOrder) => void

  return (
    <div className={classes.buttonsWrapper}>
      <button className={classes.button} onClick={() => typedSortMethod(sortUp)}>
        <img className={classes.arrowIcon} src={upArrowSrc} alt='' />
      </button>
      <button className={classes.button} onClick={() => typedSortMethod(sortDown)}>
        <img className={classes.arrowIcon} src={downArrowSrc} alt='' />
      </button>
    </div>
  )
}

export default SortButtons
