import { Autocomplete } from '@material-ui/lab'
import React from 'react'
import searchIcon from 'src/assets/icon/blue-search.svg'
import Input from '../Input'
import ListBox from './components/ListBox'
import { InputExtendedProps, Props } from './model'
// import useStyles from './useStyles'

const SingleAutoComplete: React.FC<Props> = ({ options, placeholder, onSubmit, ...restProps }) => {
  const [inputValue, setInputValue] = React.useState('')
  const { localStorage } = window

  const filterValue = (valueList: string[]) => {
    return valueList.filter((value: string) => value !== inputValue)
  }

  const saveKeyword = (valueList: string[]) => {
    inputValue && localStorage.setItem('roleKeywords', JSON.stringify(valueList))
  }

  return (
    <Autocomplete
      {...restProps}
      freeSolo
      options={options}
      ListboxComponent={ListBox}
      renderInput={({ inputProps, InputProps }) => {
        const props = inputProps as InputExtendedProps

        return (
          <div ref={InputProps.ref}>
            <form
              onSubmit={(event: React.FormEvent<HTMLFormElement>) => {
                const keyWordFromStorage = localStorage.getItem('roleKeywords')
                let currentKeyWord: string[] | []

                if (keyWordFromStorage) {
                  currentKeyWord = filterValue(JSON.parse(keyWordFromStorage)) as string[]
                  saveKeyword([...currentKeyWord, inputValue])
                } else {
                  saveKeyword([inputValue])
                }

                event.preventDefault()
                onSubmit && onSubmit(inputValue)
              }}
            >
              <Input
                inputProps={{
                  ...props,
                  onChange: (event: React.ChangeEvent<{ value: string }>) => {
                    props.onChange && props.onChange(event)
                    setInputValue(event.target.value)
                  },
                }}
                placeholder={placeholder}
                startAdornment={<img src={searchIcon} alt='' />}
                height={40}
              />
            </form>
          </div>
        )
      }}
    />
  )
}

export default SingleAutoComplete
