import { Snackbar as SnackbarComponent } from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import React from 'react'
import { ReactComponent as SuccessIcon } from 'src/assets/icon/success.svg'
import { Props } from './model'
import useStyles from './useStyles'

const SnackBar: React.FC<Props> = ({ severity, open, onClose, message, anchorOrigin }) => {
  const classes = useStyles()

  const handleClose = (event?: React.SyntheticEvent, reason?: string) => {
    if (reason === 'clickaway') {
      return
    }

    onClose(event, reason)
  }

  return (
    <SnackbarComponent className={classes.container} autoHideDuration={1000} open={open} onClose={handleClose} anchorOrigin={anchorOrigin}>
      <Alert iconMapping={{ success: <SuccessIcon /> }} onClose={handleClose} severity={severity}>
        {message}
      </Alert>
    </SnackbarComponent>
  )
}

export default SnackBar
